.total-mesas {
    background: #8c52ff;
    color: white;
    padding: 20px;
    border-radius: 12px;
    text-align: center;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
    font-size: 18px;
    font-weight: bold;
    width: 80%;
    height: 16%;
    margin: 10px auto;
  }
  
  .total-mesas h3 {
    font-size: 16px;
    margin-bottom: 5px;
  }
  
  .total-mesas p {
    font-size: 24px;
    margin: 0;
  }
  